<template>
  <header class="navbar">
    <div class="logo">My Portfolio</div>

    <!-- Hamburger icon -->
    <div class="hamburger" @click="toggleMenu">
      <span class="line"></span>
      <span class="line"></span>
      <span class="line"></span>
    </div>

    <!-- Navigation links -->
    <ul :class="['nav-links', { 'nav-active': menuOpen }]">
      <li><a href="#about" @click="selectSection('about')">About</a></li>
      <li><a href="#projects" @click="selectSection('projects')">Projects</a></li>
      <li><a href="#skills" @click="selectSection('skills')">Skills</a></li>
      <li><a href="#education" @click="selectSection('education')">Education</a></li>
      <li><a href="#experience" @click="selectSection('experience')">Experience</a></li>
      <li><a href="#certifications" @click="selectSection('certifications')">Certifications</a></li>
    </ul>
  </header>
</template>

<script>
export default {
  name: 'HeaderBar',
  data() {
    return {
      menuOpen: false,
    };
  },
  methods: {
    toggleMenu() {
      this.menuOpen = !this.menuOpen;
    },
    selectSection(section) {
      this.menuOpen = false;
      this.$emit('section-selected', section); // üî• Emit to App.vue
    }
  }
};
</script>

<style scoped>
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: white;
  padding: 1em;
  position: relative;
}

.logo {
  font-size: 1.5em;
  font-weight: bold;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 1.5em;
}

.nav-links li a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
}

.hamburger {
  display: none;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
}

.hamburger .line {
  width: 25px;
  height: 3px;
  background-color: white;
}

@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .nav-links {
    position: absolute;
    top: 60px;
    right: 0;
    background-color: #444;
    width: 100%;
    flex-direction: column;
    align-items: center;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
  }

  .nav-links.nav-active {
    max-height: 400px;
  }

  .nav-links li {
    padding: 0.8em 0;
  }
}
</style>
